<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col-md-12">
        <h1 class="display-3">Finance Dispatch</h1>
      </div>
      <div class="card col-md-3" v-for="(req, index) in reqs" :key="index">
        <div class="card-body">
          <h5 class="card-title">Stock #: {{req.stock_number}}</h5>
          <p class="card-text">Sales Associate: {{req.sales_associate}}</p>
          <p class="card-text">Deal #: {{req.deal_number}}</p>
          <p class="card-text">Customer Name: {{req.customer_name}}</p>
          <p class="card-text">Type of Vehicle: {{req.type_of_vehicle}}</p>
          <p class="card-text">Model: {{req.model}}</p>
          <hr />
          <form @submit.prevent="updateFinanceReq(completionSales, salesManager, req.id)">
            <label for="completionSales">Approval Status</label>
            <select
              id="completionSales"
              class="form-control form-control-lg"
              v-model="completionSales"
              required
            >
              <option v-for="(approval, index) in approvals" :key="index">{{ approval }}</option>
            </select>
            <hr />
            <label for="salesManager">Finance Associate</label>
            <select
              id="salesManager"
              class="form-control form-control-lg"
              v-model="salesManager"
              required
            >
              <option v-for="(man, index) in managers" :key="index">{{ man }}</option>
            </select>
            <button type="submit" class="btn btn-primary">Submit</button>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { db } from "../main";
import router from "../router";

export default {
  name: "Finance",
  data() {
    return {
      reqs: [],
      approvals: ["Claimed"],
      managers: ["Finance 1", "Finance 2", "Finance 3", "Finance 4"],
      completionSales: "",
      salesManager: ""
    };
  },
  firestore() {
    return {
      reqs: db
        .collection("makeReady")
        .where("completionSales", "==", "Approved")
        .where("completionFinance", "==", "Incomplete")
    };
  },
  methods: {
    updateFinanceReq(status, manager, id) {
      if (status !== "" && manager !== "") {
        db.collection("makeReady")
          .doc(id)
          .update({
            financeAssociate: manager,
            completionFinance: "Complete",
            financeTimestamp: new Date()
          });
      }
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1 {
  margin-top: 20px;
  margin-bottom: 30px;
}

button {
  margin-top: 15px;
}

.container-fluid {
  width: 90%;
}
</style>



