<template>
  <table class="table">
  <thead class="thead-dark">
    <tr>
      <th scope="col">#</th>
      <th scope="col">Stk #</th>
      <th scope="col">Sales Associate</th>
      <th scope="col">Sales Approval Timestamp</th>
      <th scope="col">Customer Care Package Timestamp</th>
      <th scope="col">Finance Claimed Timestamp</th>
      <th scope="col">Car Wash Claimed Timestamp</th>
      <th scope="col">Car Wash Complete Timestamp</th>
    </tr>
  </thead>
  <tbody>
    <tr v-for="(req, index) in reqs" :key="index">
      <th>{{index + 1}}</th>
      <td>{{req.stock_number}}</td>
      <td>{{req.sales_associate}}</td>
      <td>{{dateFormat(req.salesTimestamp)}}</td>
      <td>{{dateFormat(req.partsClaimedTimestamp)}}</td>
      <td>{{dateFormat(req.financeTimestamp)}}</td>
      <td>{{dateFormat(req.carwashClaimedTimestamp)}}</td>
      <td>{{dateFormat(req.carwashCompleteTimestamp)}}</td>
    </tr>
  </tbody>
</table>
</template>

<script>
import { db } from "../main";
import router from "../router";

export default {
  name: "Finance",
  data() {
    return {
      reqs: [],
    };
  },
  firestore() {
    return {
      reqs: db
        .collection("makeReady")
    };
  },
  methods: {
    dateFormat(date){
    return new Date(date.seconds * 1000).toLocaleString()
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



