<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col-md-12">
        <h1 class="display-3">Customer Care Package Dispatch</h1>
      </div>
      <div class="card col-md-3" v-for="(req, index) in reqs" :key="index">
        <div class="card-body">
          <h5 class="card-title">Stock #: {{req.stock_number}}</h5>
          <p class="card-text">Sales Associate: {{req.sales_associate}}</p>
          <p class="card-text">Customer Name: {{req.customer_name}}</p>
          <p class="card-text">Type of Vehicle: {{req.type_of_vehicle}}</p>
          <p class="card-text">Customer Care Package: {{req.customer_care_package}}</p>
          <p class="card-text">Parts Associate: {{req.partsClaimedAssociate}}</p>
          <hr />
            <label v-if='req.partsClaimed' for="completionSales">Status</label>
            <select v-if='req.partsClaimed' id="completionSales" class="form-control form-control-lg" v-model='req.partsClaimedStatus' required>
              <option></option>
              <option v-for="(approval, index) in approvals" :key="index">{{ approval }}</option>
            </select>
            <hr v-if='req.partsClaimed'/>
            <label v-if='req.partsClaimed' for="salesManager">Parts Associate</label>
            <select v-if='req.partsClaimed' id="salesManager" class="form-control form-control-lg" v-model='req.partsClaimedAssociate' required>
              <option></option>
              <option v-for="(man, index) in managers" :key="index">{{ man }}</option>
            </select>
            <button
              @click="claimed(req.id, req.partsClaimedAssociate, req.partsClaimedStatus)"
              class="btn btn-primary" v-if='req.partsClaimed'
            >Claim</button>
            <button v-if='!req.partsClaimed' @click="unclaimed(req.id, req.carWashClaimedAssociate, req.carWashClaimedStatus)" class="btn btn-primary">Unclaim</button>
            <button v-if='!req.partsClaimed' @click="complete(req.id, req.carWashClaimedAssociate, req.carWashClaimedStatus, req.deliveryBay)" class="btn btn-primary">Complete</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { db } from "../main";
import router from "../router";

export default {
  name: "Finance",
  data() {
    return {
      reqs: [],
      approvals: ["Claimed"],
      managers: [
        'Jason',
        'Krystal',
        'Martin',
        'Carlos',
        'Chris',
        'Red',
        'John',
        'Freddie',
        'Victor',
        'Daniel',
        'Kevin',
        'Alan',
        'Russ'
      ],
      delivery: ["Bay 1", "Bay 2", "Bay 3", "Bay 4", "Delivery Bay Overflow"],
      completionSales: "",
      salesManager: "",
      claimedStatus: "",
      carwashassociate: "",
      delivery_bay: ""
    };
  },
  firestore() {
    return {
      reqs: db
        .collection("makeReady")
        .where("customer_care_package", "==", "Yes").where("completionSales", "==", "Approved").where('partsClaimed', "==", true)
    };
  },
  methods: {
    claimed(id, associate, claim) {
        db.collection("makeReady")
          .doc(id)
          .update({
            partsClaimedAssociate: associate,
            partsClaimedTimestamp: new Date(),
            partsClaimedStatus: claim,
            partsClaimed: false
          });
      },
      unclaimed(id, associate, claim) {
        db.collection("makeReady")
          .doc(id)
          .update({
            carWashClaimedAssociate: '',
            carwashClaimedTimestamp: '',
            carWashClaimedStatus: '',
            claimed: true
          });
      },
      complete(id, associate, claim, bay) {
        db.collection("makeReady")
          .doc(id)
          .update({
            completionCarWash: 'Complete',
            carwashCompleteTimestamp: new Date(),
            carWashClaimedStatus: 'Complete',
            deliveryBay: bay
          });
      }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1 {
  margin-top: 20px;
  margin-bottom: 30px;
}

button {
  margin-top: 15px;
}

.container-fluid {
  width: 90%;
}
</style>



