<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col-md-12">
        <h1 class="display-3">TPO Dispatch</h1>
      </div>
      <div class="card col-md-3" v-for="(req, index) in reqs" :key="index">
        <div class="card-body">
          <h5 class="card-title">Stock #: {{req.stock_number}}</h5>
          <p class="card-text">Sales Associate: {{req.sales_associate}}</p>
          <p class="card-text">Deal #: {{req.deal_number}}</p>
          <p class="card-text">Customer Name: {{req.customer_name}}</p>
          <p class="card-text">Model: {{req.model}}</p>
          <p class="card-text">Customer Care Package: {{req.customer_care_package}}</p>
          <p class="card-text">Accessories: {{req.accessories}}</p>
          <p class="card-text" v-if="req.accessories">List Accessories: {{req.list_accessories}}</p>
          <p class="card-text">Detail Service: {{req.detail_service}}</p>
          <hr />
          <form @submit.prevent="updateSalesReq(completionSales, salesManager, req.id)">
            <label for="completionSales">Approval Status</label>
            <select
              id="completionSales"
              class="form-control form-control-lg"
              v-model="req.completionSales"
              required
            >
              <option v-for="(approval, index) in approvals" :key="index">{{ approval }}</option>
            </select>
            <hr />
            <label for="salesManager">Manager</label>
            <select
              id="salesManager"
              class="form-control form-control-lg"
              v-model="req.salesManager"
              required
            >
              <option v-for="(man, index) in managers" :key="index">{{ man }}</option>
            </select>
            <button type="submit" class="btn btn-primary">Submit</button>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { db } from "../main";
import router from "../router";

export default {
  name: "Preowned",
  data() {
    return {
      reqs: [],
      approvals: ["Approved", "Denied"],
      managers: [
        "Barry Laird",
        "Rolando Rocha",
        "Miguel Valdez",
        "Yvone Garcia",
        "Don Wilkinson",
        "Joshua Barazza"
      ],
      completionSales: "",
      salesManager: ""
    };
  },
  firestore() {
    return {
      reqs: db
        .collection("makeReady")
        .where("type_of_vehicle", "==", "Pre-owned")
        .where("completionSales", "==", "Incomplete")
    };
  },
  methods: {
    updateSalesReq(status, manager, id) {
      if (status !== "" && manager !== "") {
        if (status === "Approved") {
          db.collection("makeReady")
            .doc(id)
            .update({
              completionSales: 'Approved',
              salesManager: manager,
              completionFinance: 'Incomplete',
              completionCarWash: 'Incomplete',
              salesTimestamp: new Date ()
            });
        } else {
            db.collection("makeReady")
            .doc(id)
            .update({
              completionSales: 'Denied',
              salesManager: manager,
              salesTimestamp: new Date ()
            });
        }
      }
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1 {
    margin-top: 20px;
    margin-bottom: 30px;
}

button {
    margin-top: 15px;
}

.container-fluid {
    width: 90%;
}
</style>



