import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Newcar from '@/components/Newcar'
import Loaner from '@/components/Loaner'
import Preowned from '@/components/Preowned'
import Finance from '@/components/Finance'
import Carwash from '@/components/Carwash'
import CustomerCarePackage from '@/components/CustomerCarePackage'
import Metrics from '@/components/Metrics'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/newcar',
      name: 'Newcar',
      component: Newcar
    },
    {
      path: '/loancar',
      name: 'Loaner',
      component: Loaner
    },
    {
      path: '/preowned',
      name: 'Preowned',
      component: Preowned
    },
    {
      path: '/finance',
      name: 'Finance',
      component: Finance
    },
    {
      path: '/carwash',
      name: 'Carwash',
      component: Carwash
    },
    {
      path: '/customercarepackage',
      name: 'CustomerCarePackage',
      component: CustomerCarePackage
    },
    {
      path: '/metrics',
      name: 'Metrics',
      component: Metrics
    }
  ]
})



