<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col-md-12">
        <h1 class="display-3">Car Wash Dispatch</h1>
      </div>
      <div class="card col-md-3" v-for="(req, index) in reqs" :key="index">
        <div class="card-body">
          <h5 class="card-title">Stock #: {{req.stock_number}}</h5>
          <p class="card-text">Sales Associate: {{req.sales_associate}}</p>
          <p class="card-text">Customer Name: {{req.customer_name}}</p>
          <p class="card-text">Type of Vehicle: {{req.type_of_vehicle}}</p>
          <p class="card-text">Model: {{req.model}}</p>
          <p class="card-text">Detail Service: {{req.detail_service}}</p>
          <p class="card-text">Car Wash Associate: {{req.carWashClaimedAssociate}}</p>
          <hr />
            <label v-if='req.claimed' for="completionSales">Status</label>
            <select v-if='req.claimed' id="completionSales" class="form-control form-control-lg" v-model= 'req.carWashClaimedStatus' required>
              <option></option>
              <option v-for="(approval, index) in approvals" :key="index">{{ approval }}</option>
            </select>
            <hr v-if='req.claimed'/>
            <label v-if='req.claimed' for="salesManager">Car Wash Associate</label>
            <select v-if='req.claimed' id="salesManager" class="form-control form-control-lg" v-model='req.carWashClaimedAssociate' required>
              <option></option>
              <option v-for="(man, index) in managers" :key="index">{{ man }}</option>
            </select>
            <hr v-if='req.claimed' />
            <label v-if='!req.claimed' for="salesManager">Delivery Bay</label>
            <select v-if='!req.claimed' id="salesManager" class="form-control form-control-lg" v-model="req.deliveryBay">
              <option></option>
              <option v-for="(del, index) in delivery" :key="index">{{ del }}</option>
            </select>
            <button
              @click="claimed(req.id, req.carWashClaimedAssociate, req.carWashClaimedStatus)"
              class="btn btn-primary" v-if='req.claimed'
            >Claim</button>
            <button v-if='!req.claimed' @click="unclaimed(req.id, req.carWashClaimedAssociate, req.carWashClaimedStatus)" class="btn btn-primary">Unclaim</button>
            <button v-if='!req.claimed' @click="complete(req.id, req.carWashClaimedAssociate, req.carWashClaimedStatus, req.deliveryBay)" class="btn btn-primary">Complete</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { db } from "../main";
import router from "../router";

export default {
  name: "Finance",
  data() {
    return {
      reqs: [],
      approvals: ["In process"],
      managers: [
        "Car Wash Associate 1",
        "Car Wash Associate 2",
        "Car Wash Associate 3",
        "Car Wash Associate 4"
      ],
      delivery: ["Bay 1", "Bay 2", "Bay 3", "Bay 4", "Delivery Bay Overflow"],
      completionSales: "",
      salesManager: "",
      claimedStatus: "",
      carwashassociate: "",
      delivery_bay: ""
    };
  },
  firestore() {
    return {
      reqs: db
        .collection("makeReady")
        .where("completionSales", "==", "Approved")
        .where("completionCarWash", "==", "Incomplete")
    };
  },
  methods: {
    claimed(id, associate, claim) {
        db.collection("makeReady")
          .doc(id)
          .update({
            carWashClaimedAssociate: associate,
            carwashClaimedTimestamp: new Date(),
            carWashClaimedStatus: claim,
            claimed: false
          });
      },
      unclaimed(id, associate, claim) {
        db.collection("makeReady")
          .doc(id)
          .update({
            carWashClaimedAssociate: '',
            carwashClaimedTimestamp: '',
            carWashClaimedStatus: '',
            claimed: true
          });
      },
      complete(id, associate, claim, bay) {
        db.collection("makeReady")
          .doc(id)
          .update({
            completionCarWash: 'Complete',
            carwashCompleteTimestamp: new Date(),
            carWashClaimedStatus: 'Complete',
            deliveryBay: bay
          });
      }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1 {
  margin-top: 20px;
  margin-bottom: 30px;
}

button {
  margin-top: 15px;
}

.container-fluid {
  width: 90%;
}
</style>



