<template>
  <div class="container">
    <div class="row">
      <div class="col-sm-12">
        <h1 class="display-3 hero-heading">Make Ready Request</h1>
      </div>
      <form
        class="col-sm-12"
        @submit.prevent="newReq(sales_associate, type_of_vehicle, deal_number, customer_name, stock_number, mileage, model, customer_care_package, accessories, list_accessories, detail_service)"
      >
        <div class="form-row">
          <div class="form-group col-md-12">
            <label for="sales_associate">Sales Associate</label>
            <select
              id="sales_associate"
              class="form-control form-control-lg"
              v-model="sales_associate"
              required
            >
              <option selected>Please Select One...</option>
              <option v-for="(ass, index) in salesAssociates" :key="index">{{ ass }}</option>
            </select>
          </div>
        </div>
        <div class="form-row">
          <div class="form-group col-md-6">
            <label for="typeOfVehicle">Type Of Vehicle Sold</label>
            <select
              id="typeOfVehicle"
              class="form-control form-control-lg"
              v-model="type_of_vehicle"
              required
            >
              <option selected>Please Select One...</option>
              <option v-for="(type, index) in typeOfVehicle" :key="index">{{ type }}</option>
            </select>
          </div>
          <div class="form-group col-md-6">
            <label for="model">Vehicle Model</label>
            <select id="model" class="form-control form-control-lg" v-model="model" required>
              <option selected>Please Select One...</option>
              <option v-for="(vehicle, index) in vehicles" :key="index">{{ vehicle }}</option>
            </select>
          </div>
        </div>
        <div class="form-row">
          <div class="form-group col-md-6">
            <label for="stock_number">Stock Number</label>
            <input
              type="text"
              class="form-control form-control-lg"
              id="stock_number"
              placeholder="ex: 123456"
              v-model="stock_number"
              required
            />
          </div>
          <div class="form-group col-md-6">
            <label for="mileage">Mileage</label>
            <input
              type="text"
              class="form-control form-control-lg"
              id="mileage"
              placeholder="ex: 123,000"
              v-model="mileage"
              required
            />
          </div>
        </div>
        <div class="form-row">
          <div class="form-group col-md-6">
            <label for="deal_number">Deal Number</label>
            <input
              type="text"
              class="form-control form-control-lg"
              id="deal_number"
              placeholder="ex: 123456"
              v-model="deal_number"
            />
          </div>
          <div class="form-group col-md-6">
            <label for="customer_name">Customer Name</label>
            <input
              type="text"
              class="form-control form-control-lg"
              id="customer_name"
              placeholder="ex: John Smith"
              v-model="customer_name"
              required
            />
          </div>
        </div>
        <div class="form-row">
          <div class="form-group col-md-6">
            <label for="customer_care_package">Customer Care Package</label>
            <select
              id="customer_care_package"
              class="form-control form-control-lg"
              v-model="customer_care_package"
              required
            >
              <option selected>Please Select One...</option>
              <option v-for="(boo, index) in boolean" :key="index">{{ boo }}</option>
            </select>
          </div>
          <div class="form-group col-md-6">
            <label for="accessories">Accessories</label>
            <select
              id="accessories"
              class="form-control form-control-lg"
              v-model="accessories"
              required
            >
              <option selected>Please Select One...</option>
              <option v-for="(boo, index) in boolean" :key="index">{{ boo }}</option>
            </select>
          </div>
        </div>
        <div class="form-row">
          <div class="form-group col-md-12">
            <label for="list_accessories">List Accessories</label>
            <input
              type="text"
              class="form-control form-control-lg"
              id="list_accessories"
              placeholder="ex: Black kidney grilles..."
              v-model="list_accessories"
            />
          </div>
        </div>
        <div class="form-row">
          <div class="form-group col-md-12">
            <label for="detail_service">Detail Service</label>
            <select
              id="detail_service"
              class="form-control form-control-lg"
              v-model="detail_service"
              required
            >
              <option selected>Please Select One...</option>
              <option v-for="(service, index) in detailServices" :key="index">{{ service }}</option>
            </select>
          </div>
        </div>

        <button type="submit" class="btn btn-success">Submit Request</button>
      </form>
    </div>
  </div>
</template>

<script>
import { db } from "../main";
import router from "../router";

export default {
  name: "Home",
  data() {
    return {
      reqs: [],
      salesAssociates: [
        "Pedro Davila",
        "Michael Polak",
        "Jerry Agulair",
        "Oscar",
        "Chris Quincena"
      ],
      typeOfVehicle: ["New", "Pre-owned", "Loaner"],
      vehicles: [
        "1 series",
        "2 series",
        "3 series",
        "4 series",
        "5 series",
        "6 series",
        "7 series",
        "8 series",
        "X1",
        "X2",
        "X3",
        "X4",
        "X5",
        "X6",
        "X7",
        "Z4",
        "i8",
        "i3",
        "M"
      ],
      boolean: ["Yes", "No"],
      detailServices: ["New Car", "Pre-Owned", "Refresh"],
      sales_associate: "",
      type_of_vehicle: "",
      deal_number: "",
      customer_name: "",
      stock_number: "",
      mileage: "",
      model: "",
      customer_care_package: "",
      accessories: "",
      list_accessories: "",
      detail_service: ""
    };
  },
  firestore() {
    return {
      reqs: db.collection("makeReady")
    };
  },
  methods: {
    newReq(
      sales_associate,
      type_of_vehicle,
      deal_number,
      customer_name,
      stock_number,
      mileage,
      model,
      customer_care_package,
      accessories,
      list_accessories,
      detail_service
    ) {
      db.collection("makeReady").add({
        reqTimestamp: new Date(),
        salesTimestamp: "",
        financeTimestamp: "",
        carwashClaimedTimestamp: "",
        carwashCompleteTimestamp: "",
        sales_associate,
        type_of_vehicle,
        deal_number,
        customer_name,
        stock_number,
        mileage,
        model,
        customer_care_package,
        accessories,
        list_accessories,
        detail_service,
        completionSales: "Incomplete",
        completionFinance: "no show",
        completionCarWash: "no show",
        salesTimestamp: "",
        salesManager: "",
        financeAssociate: "",
        carWashClaimedAssociate: "",
        carWashClaimedStatus: "",
        claimed: true,
        deliveryBay: "",
        partsClaimedAssociate: "",
        partsClaimedTimestamp: "",
        partsClaimed: true,
        partsClaimedStatus: ""
      });
      (this.sales_associate = ""),
        (this.type_of_vehicle = ""),
        (this.deal_number = ""),
        (this.customer_name = ""),
        (this.stock_number = ""),
        (this.mileage = ""),
        (this.model = ""),
        (this.customer_care_package = ""),
        (this.accessories = ""),
        (this.list_accessories = ""),
        (this.detail_service = "");
      router.push("/");
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
button {
  margin-top: 3rem;
  margin-bottom: 50px;
  height: 3.5rem;
}

label {
  font-weight: bold;
  font-size: 1em;
}

.hero-heading {
  margin-bottom: 5rem;
}

.form-row {
  margin-bottom: 2rem;
}
</style>



